import React from 'react'

const Morir = () => {
    return (
        <div className="morir">
            <h1> <i class="fas fa-star-half"/> En el olvido <i class="fas fa-heart-broken"/> </h1>


            <p>


            Voy a abandonar esta casa. Pero no sin antes prenderla fuego
y exorcizarla de mi propia conciencia.
Mendigaste un hogar Y ahora qu te queda?
Nada ms que paredes 
y puertas que no abren.
Y soledad en el marco de tu habitacin
nuestra habitacin. </p>
<p>
Renegaste entre los platos sucios
ablandaste el cielo con tus manos,
surfeaste la neblina con tus pantuflas,
y atrasaste la muerte de mi todo.

Llevate cada una de estas palabras si hace falta,
pero no te marches sin tus cosas,
sin tus muebles, tu cepillo, tus heridas
tu guerra y tu paz.
Andate pero promet no volver
porque la entrada siempre esperar tus pasos,
pero creo que no puedo soportar 
verte irte de nuevo.
            </p>
        </div>
    )
}

export default Morir



