import React from 'react'
import '../../App.css';



const Encuentro = () => {
    return (
        <div className="encuentro">


<h1><i className="fas fa-sun"/> NENA <i className="fas fa-sun"/> </h1>
<p>
Me gustan tus ojos, cmo  me miras.
Me gusta tu pelo, para enredar. 
Me gustan tus dedos, y cmo te ams.</p>

<p>Pero lo que no me gusta,es cuando te vas.
Nena, dame un poco ms nena. 
Ya no quiero ahogar ms penas,
Prefiero hundirme en tu mar. </p>

<p> Si no se cumplen tus sueos,
No dejes de soar.
Si se agota tu tiempo, busc una seal.
Cundo quieras quedarte sin aliento, venime a buscar.</p>

<p> Nena, dame un poco ms nena.
Ya no quiero estar tirado, 
Prefiero tu calor, en la oscuridad.</p>

<p>La noche se acerca
Y el placer nos llama
No me mires la boca,
Tan fuerte, soy as.
Si quers un poco 
De lo que te ofrezco, 
Nos vayamos de aqu.</p>


<p>Nena, dame mucho ms nena.
Apretado entre tus dos piernas,
Encuentro la libertad.
</p>
</div>
    )
}

export default Encuentro



